.TmTodoItem{
    border-bottom:3px solid rgb(106, 157, 235);
    width: 60%;
    background-color: thistle;
    color: chocolate;
    margin: auto;
    padding: 5px;
    text-align: left;
    border-radius: 10px
}

.DeleteButton{
    background-color: red;
    color: beige;
    float: right;
    cursor: pointer;
    border: 0px;
    margin-right: 20px;
    border-radius: 50%;
}
.AddForm{
    width: 60%;
   
    margin: auto;
    padding: 5px;
    
}
nav{
    display: flex;
    justify-content: space-around;
    align-items: center;
    min-height: 10px;
    background-color: lightblue;
    color: brown;
}
.nav-Links{
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 50%;
    list-style: none;
    cursor: pointer;
}